import React from 'react'
import { graphql } from 'gatsby'

import { rhythm } from '../utils/typography'
import { Layout } from '../layout'

export default ({ data, location }) => {
  const lists = data.allMarkdownRemark.edges

  const list = lists
    .filter(({
      node
    }) => node.frontmatter.target === 'list')
    .map(({
      node
    }) => node)[0]
  const {
    siteMetadata
  } = data.site

  return (<Layout location = { location } title = { siteMetadata.title } >
    <div style = {
      {
        marginLeft: `auto`,
        marginRight: `auto`,
        maxWidth: rhythm(30),
        padding: `${rhythm(0.5)} ${rhythm(3 / 4)} ${rhythm(1.5)} ${rhythm(
            3 / 4
          )}`,
      }
    }>
    <div dangerouslySetInnerHTML = {
      {
        __html: list.html,
      }
    } />{' '}
    </div> 
    </Layout>
  )
}

export const pageQuery = graphql `
    query {
        site {
            siteMetadata {
                title
                configs {
                    countOfInitialPost
                }
            }
        }
        allMarkdownRemark(filter: { frontmatter: { target: { eq: "list" } } }) {
            edges {
                node {
                    id
                    excerpt(pruneLength: 160)
                    html
                    frontmatter {
                        title
                        date(formatString: "MMMM DD, YYYY")
                        target
                        category
                    }
                }
            }
        }
    }
`